.topbar {
    width: 100%;
    height: 36px;
    background-color: purple;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.logo_icon {
    height: 100%;
    cursor: pointer;
}

.title {
    color: white;
    font-weight: bold;
    align-self: center;
    cursor: default;
}

.menu_button {
    width: 36px;
    height: 36px;
    display: flex;
    flex-direction: column;
}

.menu_icon {
    color: white;
}